import React from "react"

import Layout from "../components/layout"
import SEO from "../components/seo"

const NotFoundPage = () => (
  <Layout>
    <SEO title="404: Not found" />
    <h1>NOT FOUND</h1>
    <p>The page you were looking for doesn't exist.</p>

    <a href={"/"}>Go back</a>
  </Layout>
)

export default NotFoundPage
